import React, {Component} from "react"

class PortfolioItemWrapper extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        const i = this.props.value.toString()
        console.log(i)
        return (
            <div className="portfolio-item-wrapper">
                <div
                    className="portfolio-img-background"
                    style={
                        {
                            backgroundImage: "url(" + require('./images/' + i + '.jpg') + ")"
                        }
                    }
                />
                <div className="img-text-wrapper">
                    <div className="logo-wrapper">
                        <img src={require("./images/logo.png")}/>
                    </div>
                    <div className="subtitle">
                        One day will never be like the other in any way or another
                    </div>
                </div>
            </div>

        )
    }
}

export default PortfolioItemWrapper