import React, {Component} from "react"
import ReactDOM from "react-dom";
import App from "./App";

class NavWrapper extends Component {
    constructor(props) {
        super(props)
        this.handleChange = this.handleChange.bind(this)

    }

    handleChange(event){
        ReactDOM.render(<App value={4}/>, document.getElementById("root"))
        console.log("df")
    }

    render() {
        return (
            <div className="nav-wrapper">
                <div className="left-side">
                    <div
                        className="home-nav-link-wrapper"
                        onClick={this.handleChange}
                    ><div>Home</div>
                    </div>
                    <div className="about-nav-link-wrapper">
                        <a href="https://unsplash.com/">About</a>
                    </div>
                </div>
                <div className="right-side">
                    <div className="brand">
                        <div className="name-tag">SHERIF MALEK</div>
                    </div>
                </div>
            </div>
        )
    }
}

export default NavWrapper